  
  
/*********** general styles **************/
html , body{
  transition: 0.5s !important;
  scroll-behavior: smooth;
  overflow-x: hidden;
}
a{
  text-decoration: none !important;
  color : unset !important;
}
.text-green{
  color : #3C7A50 !important;
}
.bg-green {
  background-color: #3C7A50 !important;
}
.bg-lightGreen{
  background-color: #F6FAF6 !important;
}
.btn-green{
  background-image: linear-gradient(#3C7A3C, #3C7A54) !important;
  color : white !important;
  padding: .5rem .75rem;
  border-radius: 0.25rem;
  border : none !important;
  border : 1px solid #3C7A3C;
  outline: none !important;
}
.btn-green:hover , .btn-green:active {
  background-image: linear-gradient(#228522, #198542) !important;
  border : 1px solid #3C7A3C; 
  outline: none !important;
}
.btn-green-outline , .btn-green-outline:hover ,.btn-green-outline:active{
  background-color: white !important;
  border : 1px solid #3C7A3C !important;
  color : #3C7A3C !important;
  padding: .5rem 0.75rem;
  border-radius: 0.25rem;
  outline: none !important;
}
.text-grey{
  color: #8E8E8E !important;
}
.border-green{
  border-bottom : 2px solid #3C7A3C !important;
  padding-bottom: 0.25rem;
}
.shadow {
  box-shadow: 0 .5rem 3rem rgba(0,0,0,.07)!important;
}
.d-web{
  display: block !important;
}
.d-mob{
  display: none !important;
}
.d-sm-mob{
  display: none !important;
}
.p-relative{
  position: relative !important;
}
.p-absolute{
  position: absolute !important;
}
.max-h-100-responsive{
  max-height: 100vh;
}
/* home */
.navbar-nav a:focus , .navbar-nav a:hover , .navbar-nav a:active{
  box-shadow: 0 .6rem 2rem rgba(0,0,0,.15)!important;
  border-radius: 4px;
  background: #3c7a3c;
  background-color: #3c7a3c;
  color: white !important;
}
.title-line{
  position: relative;
  z-index: 0;
}
.title-line::after{
  content: "";
  height: 14px;
  width: 100%;
  background-color: #e5d99542;
  position: absolute;
  left: 0;
  top: 22px;
  z-index: -1;
}
.green-line{
  position: relative;
}
.green-line::after{
  content: "";
  height: 100%;
  width: 2px;
  background-color: #3C7A3C;
  position: absolute;
  left: -35px;
  top: 0;
}
/* end home */

/* login and register */
.bg-login{
  background-image:  url(../src/images/login/background.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 100vh;
}
.bg-main{
  background-image:  url(../src/images/login/bg.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 100vh;
}
.p-bottom{
  position: absolute;
  bottom: 0;
  width: 100%;
}
 /* pin code */
 .styles_react-code-input__CRulA input{
   border : none !important;
   background-color: #E8F4E8 !important;
   margin: 0 0.3rem !important;
   border-radius: 5px !important;
   font-size: 27px !important;
 }
 .styles_react-code-input__CRulA input:focus{
    border : 1px solid #3C7A3C !important;
    color : #3C7A3C !important;
    background-color:white !important;
 }  

/************ responsive **************/

@media only screen  and (max-width : 540px){
     /* home */
    /* getInTouch */
    .d-web{
      display: none !important;
    }
    .d-mob{
      display: block !important;
    }
}
@media only screen  and (max-width : 1024px){
  .max-h-100-responsive {
    max-height: 100%;
  }
}
@media only screen  and (max-width : 414px){
  .text-center-responsive{
    text-align: center !important;
  }
  /* home */
  /* about */
  .green-line::after {
      left: -12px;
  }
}
@media only screen  and (max-width : 320px){
  .d-sm-mob{
    display: block !important;
  }
  .d-web{
    display: none !important;
  }
  .d-mob{
    display: none !important;
  }
}

